.movie_card {
  width: 200px;
  height: 300px;
  margin: 15px;
  overflow: hidden;
  border-radius: 10px;
  color: white;
  box-shadow: 0px 3px 8px rgba(0, 0, 0, 0.25);
  position: relative;
  transition: all 0.2s ease-in-out;
}

.movie_card:hover {
  transform: scale(1.08);
}

.movie_poster {
  width: 100%;
  height: 100%;
}

.movie_details {
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  padding: 10px;
  background-image: linear-gradient(rgba(0, 0, 0, 0), rgba(0, 0, 0, 1));
  display: flex;
  flex-direction: column;
  justify-content: end;
  opacity: 0;
  transition: all 0.2s ease-in-out;
}

.movie_card:hover .movie_details {
  opacity: 1;
}

.movie_details_heading {
  font-size: 16px;
  color: #ffe400;
}

.movie_date_rate {
  justify-content: space-between;
  font-size: 12px;
  font-weight: 500;
  margin: 5px 0;
}

.movie_description {
  font-size: 12px;
  font-style: italic;
}
